// import request from '../utils/request'
import { mapState } from 'vuex'
export default {
  name: 'detail',
  data () {
    return {
      show: false
    }
  },
  computed: {
    ...mapState({
      isRegAuth: state => state.user.isRegAuth,
      isReg: state => state.user.isReg,
      openid: state => state.user.openid
    })
  },
  props: {
    item: Object
  },
  methods: {
    showBox () {
      this.show = true
    },
    hideBox () {
      this.show = false
    },
    book (item) {
      if (!this.isRegAuth) {
        // 没授权过
        this.$router.push({ name: 'selType' })
      } else if (!this.isReg) {
        // 没注册过
        this.$router.push({ name: 'register' })
      } else {
        // 信息都全，往下
        console.log('book item', item)
        this._book(item)
      }
    },
    _book (item) {
      // request({
      //   url: 'http://www.zhiheyikaoqin.cn/stu/order?stuOpenid=' + this.openid + '&courserId=' + item.courseId,
      //   method: 'get'
      // }).then(res => {
      setTimeout(() => {
        const res = {
          subId: '234', // int 预约id
          stuCode: '21', // string  学生编码
          courseId: '1', //  int 课程id
          subStatus: '0', // int 课程预约状态，0：预约等待， 1：预约成功，2：预约失败，3：学生取消预约，4：教师取消预约
          stuCause: '' //  string
        }
        if (res.subStatus === '0' || res.subStatus === '1') {
          this.$vux.toast.show({
            width: '240px',
            text: '预约成功，请等待老师确认'
          })
        }
      }, 20)
    }
  }
}



